import React from 'react'
import { connect } from 'react-redux'

import styles from '../Aside.module.scss'

function User({ users }) {
    const conditionOfUserSet = Object.values(users).length > 0
    const nameData = conditionOfUserSet
        ? `${Object.values(users)[0].surname} ${Object.values(users)[0].name}`
        : ''
    const idData = conditionOfUserSet
        ? Object.values(users)[0].my_dbos.map((i, index, arr) => (
            <span key={i}>
                {
                    index >= 3
                        ? index > 3
                            ? ''
                            : '...'
                        : index !== arr.length - 1
                            ? `${i}, `
                            : i
                }
            </span>
        ))
        : ''

    return (
        <div className={styles.user}>
            <div className={styles.user__name}>{nameData}</div>
            <div className={styles.user__id}>
                {idData}
            </div>
        </div>
    )
}

const ConnectedUser = connect(
    state => ({
        users: state.users,
    }),
)(User)

export default ConnectedUser
